.App {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-size: 0;
}

.flex-row {
  display: -ms-flexbox;
  display: flex;
}

/**
 * olaf
 */
.olaf {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.olaf .bit {
  display: inline-block;
  width: 10px;
  height: 10px;
  position: relative;
}

.olaf .bit.blink::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 30%;
  background-color: #000000;
}

/**
 * text
 */
@-webkit-keyframes floating {
  0% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  10% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
  20% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  25% {
    -webkit-transform: translateY(-3px);
            transform: translateY(-3px);
  }
  30%, 100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}
@keyframes floating {
  0% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  10% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
  20% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  25% {
    -webkit-transform: translateY(-3px);
            transform: translateY(-3px);
  }
  30%, 100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

.text {
  text-align: center;
  margin-bottom: 4px;
  -webkit-animation: floating 3s ease-out infinite;
          animation: floating 3s ease-out infinite;
}

.text .bit {
  display: inline-block;
  width: 4px;
  height: 4px;
}

.text.shy {
  margin-left: 15px;
}



